<template>
    <div class="outerBox">
		<div class="itemIcon">
			<img src="@/assets/page2/triangleIcon.png" class="iconImg" alt="">
		</div>
		<div class="itemName">{{content1}}</div>
		<div class="barContainer">
			<div class="redBar" :style="{width: redLength+'%'}"></div>
			<div class="yellowBar" :style="{width: yellowLength+'%'}"></div>
			<div class="silverBar" :style="{width: silverLength+'%'}"></div>
		</div>
		<div class="totalBox">{{totalCount}}</div>
	</div>
</template>

<style lang="less" scoped>
.outerBox {
	display: flex;
	align-items: center;
	position: relative;

	.itemIcon {
		height: 100%;

		.iconImg {
			width: 100%;
			height: 100%;
		}
	}

	.itemName {
		height: 100%;
		font-size: 66.67%;
		line-height: 180%;
		color: #fff;
		font-family: 'pingfangSc', sans-serif;
	}
	.barContainer {
		margin-left: 2.11%;
		width: 66.84%;
		height: 38.1%;
		background: #2c230e;
		display: flex;

		.redBar {
			height: 100%;
			background: linear-gradient(270deg, #ed4242 0%, rgba(237,66,66,0.2) 100%);
		}

		.yellowBar {
			height: 100%;
			background: linear-gradient(270deg, #ffd03b 0%, rgba(255,208,59,0.2) 100%);
		}

		.silverBar {
			height: 100%;
			background: linear-gradient(270deg, #d9e7ff 0%, rgba(217,231,255,0.2) 100%);
		}
	}
	.totalBox {
		position: absolute;
		right: 0;
		height: 100%;
		font-size: 76.19%;
		line-height: 150%;
		font-family: 'titleFont', sans-serif;
		background: linear-gradient(180deg, #FFFFFF 42%, #FFD03B 100%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

}
</style>

<script>
export default {
	props:{
		itemName:{
			type: String
		},
		dataInput:{
			type: Object
		},
		biggest:{
			type: Number
		}
	},
	data(){
		return {
			content1: '',
			redLength: 0,
			yellowLength: 0,
			silverLength: 0,
			maxCount: 0,
			totalCount: 0
		}
	},
	mounted(){
		this.content1 = this.itemName;
		let m = this.biggest;
		m = m - m%5 + 5;
		this.maxCount = m;
		let {young, middle, elder} = this.dataInput;
		this.redLength = young * 100 / m;
		this.yellowLength = middle * 100 / m;
		this.silverLength = elder * 100 / m;
		this.totalCount = young + middle + elder;
		return false;
	}
}
</script>

